.group {
    position: relative;

    .delete-group-icon,
    .color-group-icon {
        margin-right: 5px;
    }

    svg {
        font-size: 1.5rem;

        &:hover {
            color: $colorIconsTasksHover;
        }
    }

    .group-header-container {
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;

        .group-header-right {
            >* {
                width: 120px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }

        .color-picker {
            width: 120px;
            height: 150px;
            background-color: #fff;
            z-index: 10000;
            top: -10px;
            left: 130px;
            border-radius: 5px;
            box-shadow: 0px 3px 10px -5px #484444;
            cursor: auto;
            background-color: #ffffff;

            .color-pick {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                margin: 5px;
                cursor: pointer;

                &:hover {
                    transition: 0.2s;
                    width: 32px;
                    height: 32px;
                    margin: 4px;
                }
            }
        }
    }

    .task-list {
        min-height: 35px;
    }

    .task-add {
        form {
            width: 100%;
        }

        input {
            height: 100%;
            margin: 0;
            text-align: initial;
            font-family: font1;
            font-size: 1em;
            font-weight: 700;
            background-color: #f7f7f7;
            border: 1px solid rgba(109, 109, 109, 0.35);

            &::placeholder {
                font-weight: 400;
            }

            &:hover {
                border: 1px solid $colorFontTaskGroup;
            }

            &:focus {
                border: 1px solid $buttonMainBg;
            }
        }
    }

    .drag-icon {
        svg {
            color: $mainLightGrey;
            cursor: grab;
            margin: 0px 8px 0 12px;
        }

        &:hover,
        &:active {
            svg {
                transition: 0.3s;
                color: $mainBlack;
            }
        }
    }

    .group-precent-container {
        [data-title]:hover:after {
            font-size: 1rem;
            width: max-content;
        }

        height: 25px;
        position: absolute;
        right: 0px;

        .group-precent {
            margin-right: 120px;
            width: 120px;

            .precent-bar {
                height: 100%;

                &:hover {
                    z-index: 1;
                    transform: scale(1.1);
                    transition: .3s;
                }

                &.stuck {
                    background-color: #e44258;
                }

                &.working {
                    background-color: #fdab3d;
                }

                &.done {
                    background-color: #00ca72;
                }

                &.low {
                    background-color: #7bb0ae;
                }

                &.medium {
                    background-color: #a028c7;
                }

                &.high {
                    background-color: #e82d2d;
                }
            }
        }
    }
}

.drag-over {
    transition: 0.3s ease;
    background-color: rgba($mainLightGrey, 0.3)
}